
.slider{
      width: 100%;
      height:200px;
      position: relative;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}
.content{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background:green;
    z-index: 100;
/*//  设置过渡动画*/
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.remove{
    position: absolute;
    width:200px;
    height:200px;
    background:red;
    right: 0;
    top: 0;
    color:#fff;
    text-align: center;
    font-size: 40px;
    line-height: 200px;
}
